import React from 'react';
import QuicklinkFinance from '../quicklinkFinance';
import CreditAmountBarChart from '../creditAmountBarChart';

export default function FinanceBeta() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-12 col-xl-12">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Tng hp tin thu / chi nm hin ti</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <QuicklinkFinance />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-12 col-xl-12">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Tng hp tin thu nm hin ti theo khon thu</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <CreditAmountBarChart />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }



